@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  box-sizing: border-box;
}

h1 {
  color: #eee;
  margin-bottom: 20px;
}

.app {
  padding: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.form {
  width: 100%;
  max-width: 350px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem 3rem;
  border-radius: 5%;
  background: linear-gradient(70deg, rgba(62,170,231,1) 0%, rgba(59,89,236,1) 100%);
  box-shadow: 0 0 30px -5px #111;
}

input {
  width: 100%;
  margin-bottom: 15px;
  color: #dedede;
  padding: 0.7rem 1rem;
  border: none;
  border-radius: 15px;
  background-color: #2d7eff;
  box-shadow: inset 5px -5px 14px -5px #1562de;
  transition: all 0.15s ease-in-out;
}

::placeholder {
  color: #dedede;
}

button {
  margin-top: 10px;
  margin-bottom: 15px;
  color: #dedede;
  padding: 0.7rem;
  border: none;
  border-radius: 15px;
  background-color: #2d7eff;
  width: 65%;
  box-shadow: inset 5px -5px 14px -5px #1562de;
  transition: all 0.1s ease-in-out;
}

button:hover {
  transform: scaleY(1.1);
}

input:focus, input:hover {
  outline: none;
  transform: scaleY(1.1);
}

.message {
  height: 50px;
}

button:active, button:visited {
  background-color: #2b96fe;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active{
  -webkit-box-shadow: 0 0 0 30px #2d7eff inset !important;
  -webkit-text-fill-color: #dedede !important
}
